.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.footer-logo {
  display: flex;
  align-items: center;
  gap: 20px;
}
.footer-logo p {
  color: #383838;
  font-size: 46px;
  font-weight: 700;
}
.footer-links {
  display: flex;
  list-style: none;
  gap: 50px;
  color: #252525;
  font-size: 20px;
}
.footer-links li {
  cursor: pointer;
}

.footer-social-icon {
  display: flex;
  gap: 20px;
}

.footer-icons-container {
  padding: 10px;
  padding-bottom: 6px;
  background-color: #fbfbfb;
  border: 1px solid #ebebeb;
}
.footer-copyright {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  width: 100%;
  margin-bottom: 30px;
  color: #1a1a1a;
  font-size: 20px;
}
.footer-copyright hr {
  width: 80%;
  border: none;
  border-radius: 10px;
  height: 3px;
  background-color: #c7c7c7;
}
@media(max-width:800px){
  .footer{
    gap: 20px;
  }
  .footer-logo{
    gap: 10px;
align-items: end;
  }
  .footer-logo img{
    width: 40px;
  }
  .footer-logo p{
    font-size: 25px;
  }
  .footer-links{
    font-size: 14px;
    gap: 10px;
  }
  .footer-social-icon img{
    width: 22px;
  }
  .footer-copyright{
    font-size: 13px;
  }
}
@media(max-width:500px){
  .footer {
    gap: 15px;
    padding: 20px;
    flex-direction: column;
    align-items: center;
  }
  .footer-logo {
    gap: 5px;
    align-items: center;
    text-align: center;
  }
  .footer-logo img {
    width: 30px;
  }
  .footer-logo p {
    font-size: 20px;
  }
  .footer-links {
    font-size: 12px;
    gap: 5px;
    text-align: center;
  }
  .footer-links a {
    display: block;
    margin-bottom: 5px;
  }
  .footer-social-icon {
    display: flex;
    gap: 10px;
    justify-content: center;
  }
  .footer-social-icon img {
    width: 20px;
  }
  .footer-copyright {
    font-size: 10px;
    text-align: center;
  }
}
